<template>
  <h2>1. Start your Webcam</h2>
  <div class="videos">
      <span>
        <h3>Local Stream</h3>
        <video id="webcamVideo" ref="webcamVideo" autoplay playsinline></video>
      </span>
    <span>
        <h3>Remote Stream</h3>
        <video id="remoteVideo" ref="remoteVideo" autoplay playsinline></video>
      </span>


  </div>

  <button id="webcamButton" @click="start" :disabled="webcamButtonDisabled">Start webcam</button>
  <h2>2. Create a new Call</h2>
  <button id="callButton" @click="create" :disabled="callButtonDisabled">Create Call (offer)</button>

  <h2>3. Join a Call</h2>
  <p>Answer the call from a different browser window or device</p>

  <input id="callInput" ref="callInput"/>
  <button id="answerButton" @click="join" :disabled="answerButtonDisabled">Answer</button>

  <h2>4. Hangup</h2>

  <button id="hangupButton" @click="hangup" :disabled="hangButtonDisabled">Hangup</button>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'

const servers = {
  iceServers: [
    {
      urls: ['stun:stun1.l.google.com:19302', 'stun:stun2.l.google.com:19302'],
    },
  ],
  iceCandidatePoolSize: 10,
  iceTransportPolicy: 'all'
};

export default {
  name: 'HomeView',
  components: {
    HelloWorld
  },
  data: () => ({
    localStream: null,
    remoteStream: null,
    callButtonDisabled: false,
    answerButtonDisabled: false,
    webcamButtonDisabled: false,
    hangButtonDisabled: false,
    pc: new RTCPeerConnection(servers),
    socket: null,
    callId: '',
    isCaller: false,
  }),
  created() {
    this.socket = new WebSocket('https://webrtcserverdemo-production.up.railway.app/');
    this.socket.onopen = () => {
      console.log('[WebSocket] 连接成功');
    };
    this.socket.onclose = (event) => {
      if (event.wasClean) {
        console.log(`[WebSocket] 连接正常关闭, code=${event.code}, reason=${event.reason}`);
      } else {
        console.log('[WebSocket] 连接异常断开');
      }
    };
    this.socket.onerror = (error) => {
      console.error('[WebSocket] 发生错误:', error);
    };
    this.socket.onmessage = this.onSocketMessage;
  },
  methods: {
    onSocketMessage(event) {
      const msg = JSON.parse(event.data);
      console.log("websocket msg", msg)
      if (msg.type === 'offer' && !this.isCaller) {
        this.pc.setRemoteDescription(new RTCSessionDescription(msg.offer));
      }
      if (msg.type === 'answer' && this.isCaller) {
        this.pc.setRemoteDescription(new RTCSessionDescription(msg.answer));
      }
      if (msg.type === 'candidate') {
        this.pc.addIceCandidate(new RTCIceCandidate(msg.candidate));
      }
    },
    async start() {
      console.log("webcamButton")
      this.localStream = await navigator.mediaDevices.getUserMedia({video: true, audio: true});
      this.remoteStream = new MediaStream();

      // Push tracks from local stream to peer connection
      this.localStream.getTracks().forEach((track) => {
        this.pc.addTrack(track, this.localStream);
      });

      // Pull tracks from remote stream, add to video stream
      // this.pc.ontrack = (event) => {
      //   event.streams[0].getTracks().forEach((track) => {
      //     this.remoteStream.addTrack(track);
      //   });
      // };

      this.pc.addEventListener('track', (e) => {
        const stream = e.streams[0];

        const videoTracks = stream.getVideoTracks();
        const audioTracks = stream.getAudioTracks();

        console.log('视频轨道数量:', videoTracks.length);
        console.log('音频轨道数量:', audioTracks.length);
        console.log('stream active:', stream.active, 'id:', stream.id,"readyState:",stream.getVideoTracks()[0].readyState);

        if (this.$refs.remoteVideo.srcObject !== stream && this.$refs.remoteVideo.srcObject ==null) {
          this.$refs.remoteVideo.srcObject = stream;
          console.log('pc received remote stream', stream);
        }
      })

      this.$refs.webcamVideo.srcObject = this.localStream;
      // this.$refs.remoteVideo.srcObject = this.remoteStream;

      this.callButtonDisabled = false;
      this.answerButtonDisabled = false;
      this.webcamButtonDisabled = true;
    },
    async create() {
      this.isCaller = true;
      this.callId = Math.random().toString(36).substr(2, 9); // 或由服务器生成
      this.$refs.callInput.value = this.callId;
      this.pc.onicecandidate = (event) => {
        if (event.candidate) {
          const candStr = event.candidate.candidate;
          if (candStr.includes('typ srflx')) {
            // 只发送 srflx
            this.socket.send(JSON.stringify({
              type: 'candidate',
              callId: this.callId,
              candidate: event.candidate,
            }));
            console.log('发送 srflx candidate:', event.candidate);
          } else {
            console.log('丢弃非 srflx candidate:', event.candidate);
          }
        }
      };
      const offerDescription = await this.pc.createOffer();
      await this.pc.setLocalDescription(offerDescription);
      this.socket.send(JSON.stringify({
        type: 'offer',
        callId: this.callId,
        offer: offerDescription,
      }));
      this.hangButtonDisabled = false;
    },
    async join() {
      this.isCaller = false;
      this.callId = this.$refs.callInput.value;
      console.log("join", this.callId)
      this.pc.onicecandidate = (event) => {
        if (event.candidate) {
          this.socket.send(JSON.stringify({
            type: 'candidate',
            callId: this.callId,
            candidate: event.candidate,
          }));
        }
      };
      this.socket.send(JSON.stringify({
        type: 'join',
        callId: this.callId,
      }));
      // offer 会通过 onSocketMessage 收到
      // 生成 answer
      this.pc.onnegotiationneeded = async () => {
        const answerDescription = await this.pc.createAnswer();
        await this.pc.setLocalDescription(answerDescription);
        this.socket.send(JSON.stringify({
          type: 'answer',
          callId: this.callId,
          answer: answerDescription,
        }));
      };
    },
    async hangup() {
      //todo
    }
  }
}
</script>

<style>
video {
  width: 40vw;
  height: 30vw;
  margin: 2rem;
  background: #2c3e50;
}

.videos {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
